// Êï∞ÊçÆÂä†ËΩΩ
const loadSettlements = async () => {
  console.log('üîÑ ÂºÄÂßãÂä†ËΩΩÁªìÁÆóÊï∞ÊçÆ...')
  try {
    settlementLoading.value = true
    
    // ÊûÑÂª∫Êü•ËØ¢ÂèÇÊï∞
    const params: any = {
      page: 1,
      size: 50
    }
    
    // Âè™Ê∑ªÂä†ÊúâÂÄºÁöÑÂèÇÊï∞
    if (settlementSearchParams.startDate) {
      params.startDate = settlementSearchParams.startDate
    }
    if (settlementSearchParams.endDate) {
      params.endDate = settlementSearchParams.endDate
    }
    if (settlementSearchParams.isSettled !== undefined && settlementSearchParams.isSettled !== null) {
      params.isSettled = settlementSearchParams.isSettled
    }
    
    console.log('üìã Êü•ËØ¢ÂèÇÊï∞:', {
      merchant: settlementSearchParams.merchantId ? `ÂïÜÂÆ∂ID: ${settlementSearchParams.merchantId}` : 'ÂÖ®ÈÉ®ÂïÜÂÆ∂',
      dateRange: settlementSearchParams.startDate && settlementSearchParams.endDate 
        ? `${settlementSearchParams.startDate} ~ ${settlementSearchParams.endDate}` 
        : 'ÂÖ®ÈÉ®Êó∂Èó¥',
      isSettled: settlementSearchParams.isSettled !== undefined 
        ? (settlementSearchParams.isSettled ? 'Â∑≤ÁªìÁÆó' : 'Êú™ÁªìÁÆó') 
        : 'ÂÖ®ÈÉ®Áä∂ÊÄÅ',
      apiParams: params
    })
    
    let response
    let apiName
    if (settlementSearchParams.merchantId) {
      // Ê†πÊçÆÂïÜÂÆ∂IDËé∑ÂèñÁªìÁÆóÂçï
      apiName = 'getSettlementsByMerchant'
      console.log(`üåê Ë∞ÉÁî®API: ${apiName}`, { merchantId: settlementSearchParams.merchantId, params })
      apiRequestCount.value++
      response = await getSettlementsByMerchant(settlementSearchParams.merchantId, params)
      successfulApiCount.value++
    } else {
      // Ëé∑ÂèñÊâÄÊúâÁªìÁÆóÂçï
      apiName = 'getSettlements'
      console.log(`üåê Ë∞ÉÁî®API: ${apiName}`, { params })
      apiRequestCount.value++
      response = await getSettlements(params)
      successfulApiCount.value++
    }
    
    console.log(`‚úÖ ${apiName} APIÂìçÂ∫î:`, {
      status: 'success',
      dataCount: response.settlements ? response.settlements.length : 0,
      totalCount: response.total || 0,
      response: response
    })
    
    const settlements = response.settlements || []
    if (settlements.length === 0) {
      console.log('‚ö†Ô∏è Êú™Ëé∑ÂèñÂà∞ÁªìÁÆóÂçïÊï∞ÊçÆ')
      settlementData.value = []
      return
    }
    
    console.log(`üîÑ ÂºÄÂßãÂä†ËΩΩ ${settlements.length} ‰∏™ÁªìÁÆóÂçïÁöÑËØ¶ÁªÜ‰ø°ÊÅØ...`)
    
    // ‰∏∫ÊØè‰∏™ÁªìÁÆóÂçïÂä†ËΩΩËØ¶ÁªÜ‰ø°ÊÅØ
    const settlementsWithDetails = await Promise.all(
      settlements.map(async (settlement, index) => {
        try {
          console.log(`üìù [${index + 1}/${settlements.length}] Âä†ËΩΩÁªìÁÆóÂçïËØ¶ÊÉÖ: ${settlement.settleId}`)
          apiRequestCount.value++
          const detail = await getSettlementDetail(settlement.settleId)
          successfulApiCount.value++
          
          console.log(`‚úÖ [${index + 1}/${settlements.length}] ËØ¶ÊÉÖÂä†ËΩΩÊàêÂäü:`, {
            settleId: settlement.settleId,
            grossAmount: detail.grossAmount,
            commissionFee: detail.commissionFee,
            netAmount: detail.netAmount
          })
          
          const commissionRate = detail.grossAmount > 0 ? (detail.commissionFee / detail.grossAmount * 100) : 0
          
          return {
            ...settlement,
            ...detail,
            commissionRate: commissionRate
          }
        } catch (error) {
          failedApiCount.value++
          console.error(`‚ùå [${index + 1}/${settlements.length}] Ëé∑ÂèñÁªìÁÆóÂçï ${settlement.settleId} ËØ¶ÊÉÖÂ§±Ë¥•:`, {
            error: error.message || error,
            fullError: error
          })
          // Â¶ÇÊûúËé∑ÂèñËØ¶ÊÉÖÂ§±Ë¥•ÔºåËøîÂõûÂü∫Á°Ä‰ø°ÊÅØ
          return {
            ...settlement,
            grossAmount: 0,
            commissionFee: 0,
            netAmount: 0,
            commissionRate: 0
          }
        }
      })
    )
    
    console.log('üéâ ÊâÄÊúâÁªìÁÆóÂçïÊï∞ÊçÆÂä†ËΩΩÂÆåÊàê:', {
      ÊÄªÊï∞Èáè: settlementsWithDetails.length,
      ÊàêÂäüÂä†ËΩΩËØ¶ÊÉÖ: settlementsWithDetails.filter(s => s.grossAmount > 0 || s.netAmount > 0).length,
      ËØ¶ÊÉÖÂä†ËΩΩÂ§±Ë¥•: settlementsWithDetails.filter(s => s.grossAmount === 0 && s.netAmount === 0).length,
      APIË∞ÉÁî®ÁªüËÆ°: {
        ÊÄªËØ∑Ê±ÇÊï∞: apiRequestCount.value,
        ÊàêÂäüËØ∑Ê±Ç: successfulApiCount.value,
        Â§±Ë¥•ËØ∑Ê±Ç: failedApiCount.value
      }
    })
    
    settlementData.value = settlementsWithDetails
  } catch (error) {
    failedApiCount.value++
    console.error('‚ùå Âä†ËΩΩÁªìÁÆóÊï∞ÊçÆÂ§±Ë¥•:', {
      ÈîôËØØ‰ø°ÊÅØ: (error as Error).message || error,
      ÂÆåÊï¥ÈîôËØØ: error,
      ÊêúÁ¥¢Êù°‰ª∂: settlementSearchParams,
      APIË∞ÉÁî®ÁªüËÆ°: {
        ÊÄªËØ∑Ê±ÇÊï∞: apiRequestCount.value,
        ÊàêÂäüËØ∑Ê±Ç: successfulApiCount.value,
        Â§±Ë¥•ËØ∑Ê±Ç: failedApiCount.value
      }
    })
    message.error('Âä†ËΩΩÁªìÁÆóÊï∞ÊçÆÂ§±Ë¥•')
  } finally {
    settlementLoading.value = false
    console.log('üèÅ ÁªìÁÆóÊï∞ÊçÆÂä†ËΩΩÊµÅÁ®ãÁªìÊùü', {
      ÊúÄÁªàÊï∞ÊçÆÊù°Êï∞: settlementData.value.length,
      APIË∞ÉÁî®ÁªüËÆ°: {
        ÊÄªËØ∑Ê±ÇÊï∞: apiRequestCount.value,
        ÊàêÂäüËØ∑Ê±Ç: successfulApiCount.value,
        Â§±Ë¥•ËØ∑Ê±Ç: failedApiCount.value,
        ÊàêÂäüÁéá: apiRequestCount.value > 0 ? `${(successfulApiCount.value / apiRequestCount.value * 100).toFixed(1)}%` : 'N/A'
      }
    })
  }
}

const formatPeriodText = (start?: string, end?: string): string => {
  if (!start || !end) return '-'
  
  const startDate = new Date(start)
  const endDate = new Date(end)
  
  const startStr = startDate.toLocaleDateString('zh-CN')
  const endStr = endDate.toLocaleDateString('zh-CN')
  
  return `${startStr} ~ ${endStr}`
}

// Ê∑ªÂä†‰∏Ä‰∏™ÁÆÄÂçïÁöÑËØ∑Ê±ÇËÆ°Êï∞Âô®ÔºåÁî®‰∫éË∞ÉËØï
const apiRequestCount = ref(0)
const successfulApiCount = ref(0)
const failedApiCount = ref(0)

// ÁîüÂëΩÂë®Êúü
onMounted(() => {
  console.log('üöÄ ÁªìÁÆóÁÆ°ÁêÜÈ°µÈù¢ÂàùÂßãÂåñÔºåÂºÄÂßãÂä†ËΩΩÊï∞ÊçÆ...')
  console.log('üìä Ë∞ÉËØïÁªüËÆ°ÂàùÂßãÂåñ:', { 
    apiRequestCount: apiRequestCount.value,
    successfulApiCount: successfulApiCount.value,
    failedApiCount: failedApiCount.value
  })
  loadSettlements()
})
</script>

