import { get, post } from '../utils/request.js';

/** ÁªìÁÆóÂçïÂü∫Á°Ä‰ø°ÊÅØ */
export interface SettlementDto {
  settleId: string;
  periodStart: string;
  periodEnd: string;
  settledAt?: string;
}

/** ÁªìÁÆóÂçïËØ¶ÊÉÖ‰ø°ÊÅØ */
export interface SettlementDetailDto {
  settleId: string;
  periodStart: string;
  periodEnd: string;
  grossAmount: number;
  commissionFee: number;
  netAmount: number;
  settledAt?: string;
}

/** Êâ©Â±ïÁöÑÁªìÁÆóÂçï‰ø°ÊÅØÔºàÁî®‰∫éÂâçÁ´ØÊòæÁ§∫Ôºâ */
export interface SettlementItem extends SettlementDetailDto {
  merchantId?: string;
  merchantName?: string;
  statusText?: string;
  commissionRate?: number;
}

/** ÁªìÁÆóÊü•ËØ¢ÂèÇÊï∞ */
export interface SettlementQuery {
  startDate?: string;
  endDate?: string;
  isSettled?: boolean;
  page?: number;
  size?: number;
}

/** ÁªìÁÆóÂàóË°®ÂìçÂ∫î */
export interface SettlementListResponse {
  settlements: SettlementItem[];
  total: number;
}

/**
 * Ëé∑ÂèñÁªìÁÆóÂçïÂàóË°®
 * GET /api/settlements
 */
export function getSettlements(params: SettlementQuery = {}): Promise<SettlementListResponse> {
  // ÊûÑÂª∫Êü•ËØ¢ÂèÇÊï∞ÔºåËøáÊª§ÊéâundefinedÂÄº
  const queryParams: Record<string, any> = {};
  if (params.startDate) queryParams.startDate = params.startDate;
  if (params.endDate) queryParams.endDate = params.endDate;
  if (params.isSettled !== undefined) queryParams.isSettled = params.isSettled;
  if (params.page) queryParams.page = params.page;
  if (params.size) queryParams.size = params.size;
  else queryParams.size = 50;
  
  return get('/api/settlements', queryParams).then((response: any) => {
    // Â§ÑÁêÜÂêéÁ´ØÂåÖË£ÖÁöÑÂìçÂ∫îÊ†ºÂºè
    const responseData = response.data || response;
    const settlements: SettlementItem[] = responseData.map((item: SettlementDto) => ({
      ...item,
      grossAmount: 0, // Âü∫Á°ÄÂàóË°®‰∏çÂåÖÂê´Ëøô‰∫õÂ≠óÊÆµÔºåÈúÄË¶ÅÈÄöËøáËØ¶ÊÉÖÊé•Âè£Ëé∑Âèñ
      commissionFee: 0,
      netAmount: 0,
      statusText: item.settledAt ? 'Â∑≤ÁªìÁÆó' : 'ÂæÖÁªìÁÆó'
    }));
    
    return {
      settlements,
      total: settlements.length
    };
  });
}

/**
 * Ëé∑ÂèñÁªìÁÆóÂçïËØ¶ÊÉÖ
 * GET /api/settlements/{settlementId}
 */
export function getSettlementDetail(settleId: string): Promise<SettlementItem> {
  if (!settleId) {
    return Promise.reject(new Error('ÁªìÁÆóÂçïID‰∏çËÉΩ‰∏∫Á©∫'));
  }
  
  return get(`/api/settlements/${settleId}`).then((response: any) => {
    // Â§ÑÁêÜÂêéÁ´ØÂåÖË£ÖÁöÑÂìçÂ∫îÊ†ºÂºè
    const responseData = response.data || response;
    const result: SettlementItem = {
      ...responseData,
      statusText: responseData.settledAt ? 'Â∑≤ÁªìÁÆó' : 'ÂæÖÁªìÁÆó',
      commissionRate: responseData.grossAmount > 0 ? (responseData.commissionFee / responseData.grossAmount * 100) : 0
    };
    
    return result;
  });
}

/**
 * Ê†πÊçÆÂïÜÂÆ∂IDËé∑ÂèñÁªìÁÆóÂçïÂàóË°®
 * GET /api/settlements/merchants/{merchantId}
 */
export function getSettlementsByMerchant(merchantId: string, params: SettlementQuery = {}): Promise<SettlementListResponse> {
  if (!merchantId) {
    return Promise.reject(new Error('ÂïÜÂÆ∂ID‰∏çËÉΩ‰∏∫Á©∫'));
  }
  
  // ÊûÑÂª∫Êü•ËØ¢ÂèÇÊï∞ÔºåËøáÊª§ÊéâundefinedÂÄº
  const queryParams: Record<string, any> = {};
  if (params.startDate) queryParams.startDate = params.startDate;
  if (params.endDate) queryParams.endDate = params.endDate;
  if (params.isSettled !== undefined) queryParams.isSettled = params.isSettled;
  if (params.page) queryParams.page = params.page;
  if (params.size) queryParams.size = params.size;
  else queryParams.size = 50;
  
  return get(`/api/settlements/merchants/${merchantId}`, queryParams).then((response: any) => {
    // Â§ÑÁêÜÂêéÁ´ØÂåÖË£ÖÁöÑÂìçÂ∫îÊ†ºÂºè
    const responseData = response.data || response;
    const settlements: SettlementItem[] = responseData.map((item: SettlementDto) => ({
      ...item,
      merchantId,
      grossAmount: 0, // Âü∫Á°ÄÂàóË°®‰∏çÂåÖÂê´Ëøô‰∫õÂ≠óÊÆµÔºåÈúÄË¶ÅÈÄöËøáËØ¶ÊÉÖÊé•Âè£Ëé∑Âèñ
      commissionFee: 0,
      netAmount: 0,
      statusText: item.settledAt ? 'Â∑≤ÁªìÁÆó' : 'ÂæÖÁªìÁÆó'
    }));
    
    return {
      settlements,
      total: settlements.length
    };
  });
}


